export default "import {\n  CornerstoneImage,\n  CornerstoneSingleImage,\n  InsightViewer,\n  installWADOImageLoader,\n  unloadImage,\n  useViewportMirroring,\n} from '@lunit/insight-viewer';\nimport React, { useMemo, useRef } from 'react';\n\ninstallWADOImageLoader();\n\nexport default () => {\n  const image1: CornerstoneImage = useMemo(\n    () =>\n      new CornerstoneSingleImage(`wadouri:https://fixtures.front.lunit.io/dcm-files/series/CT000010.dcm`, {\n        unload: unloadImage,\n      }),\n    [],\n  );\n\n  const image2: CornerstoneImage = useMemo(\n    () =>\n      new CornerstoneSingleImage(`wadouri:https://fixtures.front.lunit.io/dcm-files/series/CT000020.dcm`, {\n        unload: unloadImage,\n      }),\n    [],\n  );\n\n  const image3: CornerstoneImage = useMemo(\n    () =>\n      new CornerstoneSingleImage(`wadouri:https://fixtures.front.lunit.io/dcm-files/series/CT000030.dcm`, {\n        unload: unloadImage,\n      }),\n    [],\n  );\n\n  const viewer2 = useRef<InsightViewer>(null);\n\n  const viewer3 = useRef<InsightViewer>(null);\n\n  const { updateMasterRenderData } = useViewportMirroring(viewer2, viewer3);\n\n  const resetTime = useMemo<number>(() => Date.now(), []);\n\n  return (\n    <div style={{ display: 'flex' }}>\n      <InsightViewer\n        width={400}\n        height={500}\n        invert={false}\n        flip={false}\n        pan={true}\n        adjust={false}\n        zoom={true}\n        resetTime={resetTime}\n        image={image1}\n        updateCornerstoneRenderData={updateMasterRenderData}\n      />\n      <InsightViewer\n        ref={viewer2}\n        width={400}\n        height={500}\n        invert={false}\n        flip={false}\n        pan={true}\n        adjust={false}\n        zoom={true}\n        resetTime={resetTime}\n        image={image2}\n        updateCornerstoneRenderData={() => {}}\n      />\n      <InsightViewer\n        ref={viewer3}\n        width={400}\n        height={500}\n        invert={false}\n        flip={false}\n        pan={true}\n        adjust={false}\n        zoom={true}\n        resetTime={resetTime}\n        image={image3}\n        updateCornerstoneRenderData={() => {}}\n      />\n    </div>\n  );\n};\n"